import "../App.css";

export default function About() {
  return (
    <section class="section is-fullheight is-fullheight-with-navbar" id="about">
      <div class="container">
        <div class="section-heading">
          <h1 class="title is-2">About Me</h1>
          <h2 class="subtitle is-4">
            <div class="columns">
              <div class="column">
                <figure class="image">
                  <img alt="" src={process.env.PUBLIC_URL + "head.jpg"} />
                </figure>
              </div>
              <div class="column">
                <p>
                  My name is Krijan Prajapati. I am a final year software engineering student sub majoring in Data Analytics.

                  I have actively worked for different organizations and gained technical skills and soft skills. 

                  I have strong interest in using data analytics to solve complex business problems. Arranging data in meaningful manner, organzing and shaping predictions is what I specialise in. As the findings from this data can shape business strategy. 

                  I am currently working for Commonwealth Bank of Australia as Machine Learning Engineer and utilising cloud technology for the workflow.

                </p>
                <br/>
                <a
                  class="button is-dark"
                  href={
                    process.env.PUBLIC_URL + "Krijan_Resume.pdf"
                  }
                  target="_blank"
                  rel="noreferrer noopener"
                >
                  View Resume
                </a>
              </div>
            </div>
          </h2>
        </div>
      </div>
    </section>
  );
}
